import React, { Fragment } from 'react'
import { useStaticQuery, graphql } from 'gatsby'
import Img from 'gatsby-image'
import styled from '@emotion/styled'
import { Card } from 'antd'
import { darken } from 'polished'

import Container from '../components/Container'
import Space from '../components/Space'
import Link from '../components/Link'
import { Title } from '../styles/typography'

import theme from '../styles/theme'
import { media, rem } from '../styles/tools'

const Grid = styled.div({
  display: 'grid',
  gridTemplateColumns: 'repeat(var(--cols, 1), 1fr)',
  [media.tablet]: { '--cols': 2 },
  [media.tabletLg]: { '--cols': 3 },
  gridGap: rem(30),
})

const Item = ({ text, to, href, img, alt = '', ...props }) => (
  <Link {...(to ? { to } : { out: true, href })} {...props}>
    <Card
      hoverable
      cover={
        typeof img === 'object' ? (
          <Img fluid={img} alt={alt} />
        ) : (
          <img src={img} alt={alt} />
        )
      }
    >
      <Card.Meta
        title={
          <Title as="h3" css={{ whiteSpace: 'normal' }}>
            {text}
          </Title>
        }
      />
    </Card>
  </Link>
)

